import request from '@/request'

let url = '/manage/building'
// 增加楼盘
export function addBuilding(name,address){
    return request({
        url,
        method:'post',
        data:{
            name:name,
            address:address
        }
    })
}
//删除楼盘
export function deleteBuilding(buildingId){
    return request({
        url,
        method:'delete',
        data:{
            buildingId:buildingId
        }
    })
}
//更新楼盘
export function updateBuilding(buildingId,name,address){
    return request({
        url,
        method:'put',
        data:{
            buildingId:buildingId,
            name:name,
            address:address 
        }
    })
}

//分页查询楼盘
export function queryBuilding(page,pageSize,name,address){
    return request({
        url,
        method:'get',
        params:{
            page:page,
            pageSize:pageSize,
            name:name,
            address:address
        }
    })
}
// 获取楼盘提示信息
export function getBuildingTip(){
    return request({
        url:'/manage/buildingTip',
        method:'get'
    })
}