<template>
  <div>
    <el-container>
      <!--      顶级侧边栏（左侧）-->
      <LeftAside :isCollapse="isCollapse" />
      <!--      顶级核心内容（右侧）-->
      <MMain :changeCollapse="changeCollapse"/>
    </el-container>
  </div>
</template>

<script>
import LeftAside from "./LeftAside";
import MMain from "./MMain";
export default {
  name: "MIndex",
  components: { LeftAside, MMain },
  data() {
    return {
      //右边菜单是否折叠
      isCollapse: false
    }
  },
  methods: {
    //父类定义回调函数，给子类修改侧边的展开与关闭
    changeCollapse(){
      this.isCollapse=!this.isCollapse
    }

  },
  created() {
    if (this.$cookies.get("username") === null) {
      this.$router.push("/login")
    }
  },
  beforeRouteEnter(to, from, next) {

    next()
  }

}
</script>
<style>
.el-container {
  border: 2px solid #eee;
  border-top: 0;
  height: 100%;
}
</style>