import request from '@/request'
let url = '/manage/meterReading'

export function queryMeterReading(page,pageSize,buildingName,houseNumber,readingDate){
    return request({
        url,
        method:'get',
        params:{
            page:page,
            pageSize:pageSize,
            buildingName: buildingName,
            houseNumber: houseNumber,
            readingDate: readingDate,
        }
    })
}
export function addMeterReading(buildingId,roomId,readingDate,waterReading,electricityReading){
    return request({
        url,
        method:'post',
        data:{
            // buildingId:buildingId,
            roomId,
            readingDate,
            waterReading,
            electricityReading
        }
    })
}
export function updateMeterReading(id,readingDate,waterReading,electricityReading){
    return request({
        url,
        method:'put',
        data:{
            id,
            readingDate,
            waterReading,
            electricityReading
        }

    })
}

export function deleteMeterReading(id){
    return request({
        url,
        method:'delete',
        params:{
            id
        }

    })
}
export function getExcelTemplate(buildingId){
    return request({
        url:"/manage/meterReading/downloadExcel",
        method:'get',
        params:{
            buildingId
        }
    })
}
export function handleList(meterReadingExcelParam,readingDate){
    return request({
        url:'/manage/meterReading/handleList',
        method:'post',
        data:{
           meterReadingExcelParam,
           readingDate
        }
    })
}
export function waterAndElectricityRank(date){
    return request({
        url:'/manage/meterReading/waterAndElectricityRank',
        method:'get',
        params:{
            date
        }
    })
}