<template>
  <div>
    <div style="text-align:center;">
      <h3>录入账户信息</h3>
    </div>
    <el-form ref="form" :model="form" :rules="rules" label-width="150px">
      <el-form-item prop="choose" label="角色名" required>
        <el-select v-model="form.choose" clearable placeholder="选择角色">
          <el-option v-for="item in form.roles" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop="username" label="用户名(登录账号)" required>
        <el-input v-model="form.username"></el-input>
      </el-form-item>
      <el-form-item prop="password" required label="用户密码">
        <el-input v-model="form.password" show-password></el-input>
      </el-form-item>
      <el-form-item prop="name" label="姓名" required>
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item prop="phoneNumber" label="电话号码" required>
        <el-input v-model="form.phoneNumber"></el-input>
      </el-form-item>
      <el-form-item label="性别">
          <template>
            <el-radio v-model="form.sex" label="0">女</el-radio>
            <el-radio v-model="form.sex" label="1">男</el-radio>
            <el-radio v-model="form.sex" label="2">未知</el-radio>
          </template>
        </el-form-item>
      <el-form-item prop="identity" label="身份证号码">
        <el-input v-model="form.identity"></el-input>
      </el-form-item>
      <el-form-item label="锁定账号">
        <el-switch v-model="form.locked" active-color="#ff4949" inactive-color="#13ce66" active-text="已锁定"
          inactive-text="未锁定">
        </el-switch>
      </el-form-item>
      <el-form-item label="账号可用">
        <el-switch v-model="form.enable" active-color="#13ce66" inactive-color="#ff4949" active-text="可用"
          inactive-text="不可用">
        </el-switch>
      </el-form-item>


      <el-form-item>
        <el-button type="primary" @click="addRenter('form')">立即创建</el-button>
        <el-button @click="$router.go(-1)">取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { addRenter, queryRoles } from '@/api/renter';
export default {
  name: "InsertRenter",
  data() {
    let validateUsername = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入用户名'));
      } else if (!(4 <= value.length && value.length <= 20)) {
        callback(new Error('用户名不得少于4个字符,不超过20'));
      } else if (!/^[\u4E00-\u9FA5A-Za-z0-9]+$/.test(value)) {
        callback(new Error('用户名只能中文、字母和数字，不能包含特殊字符'))
      } else {
        callback();
      }
    };
    let validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else if (!/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z\\W]{6,18}$/.test(value)) {
        callback(new Error('密码必须包含数字和字母，且长度为6~18'))
      } else {
        callback();
      }
    };
    let checkPhone = (rule, value, callback) => {
      if (value === '') callback(new Error('请输入手机号'))
      else if (!/^1\d{10}$/.test(value)) callback(new Error('请输入正确的手机号'))
      else callback()
    };
    let checkIdentity = (rule, value, callback) => {
      if (value === '') callback()
      else if (!/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/.test(value)) callback(new Error('请输入正确的中国大陆身份证号'))
      else callback()
    };
    return {
      form: {
        choose: '',
        roles: [],
        username: '',
        password: '',
        sex: '2',
        name: '',
        phoneNumber: '',
        identity: '',
        locked: false,
        enable: true,
      },
      rules: {
        choose: [
          { required: true, message: "请选择角色名", trigger: 'blur' }
        ],
        username: [
          // {required: true, message: '请输入用户名', trigger: 'blur'},, message: '用户名不得少于4个字符,不超过20'
          { required: true, validator: validateUsername, trigger: 'blur' },
        ],
        password: [
          { required: true, validator: validatePass, trigger: 'blur' }
        ],
        phoneNumber: [
          { required: true, validator: checkPhone, trigger: 'blur' }
        ],
        name: [
          { required: true, message: "请输入姓名", trigger: 'blur' }
        ],
        identity: [
          { validator: checkIdentity, trigger: 'blur' }
        ]
      },
    }
  },
  methods: {
    addRenter(formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) this.$message.error("表单信息填写错误！")
        else {
          let that = this.form
          addRenter(
            that.choose, that.name, that.sex, that.identity, that.username,
            that.password, that.phoneNumber, that.locked ? '1' : '0', that.enable ? '1' : '0'
          ).then(() => {
            this.$message.success("提交成功")
            this.$router.go(-1)
          })
        }
      })
    }
  },
  created() {
    queryRoles().then(res=>{
      res.data.forEach(item => {
        let temp = {
          value: item.roleId,
          label: item.description
        }
        this.form.roles.push(temp)
      })
    })

  }
}
</script>

<style scoped>
.el-form {
  margin-top: 20px;
  padding: 0 50px;
}

.el-input {
  width: 24%;
}
</style>