<template>
  <div>
    <div style="text-align:center;">
      <h3>修改账户信息</h3>
    </div>
    <el-form v-if="isHasData" ref="form" :model="form" label-width="150px">
      <el-form-item label="角色名" required>
        <el-select v-model="form.choose" clearable placeholder="选择角色名">
          <el-option v-for="item in form.roles" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="用户名(登录账号)" required>
        <el-input v-model="form.username"></el-input>
      </el-form-item>
      <el-form-item label="新密码">
        <el-tooltip class="item" effect="dark" content="留空表示密码不变" placement="right">
          <el-input v-model="form.password" show-password></el-input>
        </el-tooltip>
      </el-form-item>
      <el-form-item label="姓名" required>
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="性别">
        <template>
          <el-radio v-model="form.sex" label="0">女</el-radio>
          <el-radio v-model="form.sex" label="1">男</el-radio>
          <el-radio v-model="form.sex" label="2">未知</el-radio>
        </template>
      </el-form-item>

      <el-form-item label="电话号码" required>
        <el-input v-model="form.phoneNumber"></el-input>
      </el-form-item>
      <el-form-item label="身份证号码">

        <el-input placeholder="默认为空" v-model="form.identity"></el-input>
      </el-form-item>
      <el-form-item label="锁定账号">
        <el-switch v-model="form.locked" active-color="#ff4949" inactive-color="#13ce66" active-text="已锁定"
          inactive-text="未锁定">
        </el-switch>
      </el-form-item>
      <el-form-item label="账号可用">
        <el-switch v-model="form.enable" active-color="#13ce66" inactive-color="#ff4949" active-text="可用"
          inactive-text="不可用">
        </el-switch>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="updateRenter">确认修改</el-button>
        <el-button @click="$router.go(-1)">取消</el-button>
      </el-form-item>
    </el-form>
    <div v-else style="text-align: center;color: red;font-size: 30px;padding: 50px 0">
      <h1>错误！！请先在左边查询租客信息！</h1>
    </div>

  </div>
</template>

<script>
import { updateRenter } from '@/api/renter'

export default {
  name: "InsertRenter",
  data() {
    return {
      isHasData: false,
      form: {
        choose: '',
        roles: [
          {
            value: 1,
            label: "客户"
          }, {
            value: 2,
            label: "房东"
          }, {
            value: 3,
            label: "超级用户"
          },
        ],
        username: '',
        password: '',
        sex: '2',
        name: '',
        phoneNumber: '',
        identity: '',
        locked: false,
        enable: true,
        userId: ''
      }
    }
  },
  computed: {
    tableData() {
      return JSON.parse(this.$route.query.tableData)
    }
  },
  methods: {
    updateRenter() {
      let data = this.form;
      let renter = {
        userId: data.userId,
        roleId: data.choose,
        username: data.username,
        password: data.password,
        name: data.name,
        sex: data.sex,
        phoneNumber: data.phoneNumber,
        identity: data.identity,
        locked: data.locked ? '1' : '0',
        enable: data.enable ? '1' : '0'
      }
      updateRenter(renter).then(() => {
        this.$message.success("更新用户信息用户成功！")
        this.$router.go(-1);
      })

    }
  },
  created() {
    if (this.$route.query.isHasData !== undefined) {
      this.isHasData = true;
      let data = this.tableData;
      this.form.choose = data.roleId
      this.form.username = data.username
      this.form.password = data.password
      this.form.sex = data.sex
      this.form.name = data.name
      this.form.phoneNumber = data.phoneNumber
      this.form.identity = data.identity
      this.form.locked = data.locked
      this.form.enable = data.enable
      this.form.userId = data.userId
    }
  }
}
</script>

<style scoped>
.el-form {
  margin-top: 20px;
  padding: 0 50px;
}

.el-input {
  width: 24%;
}
</style>