<template>
  <div>
    <header style="text-align:center;margin-bottom: 10px;">
      <h3>房屋与租用关系管理</h3>
    </header>
    <!-- 搜索栏 -->
    <div>
      <el-form class="search-form" :model="searchForm" :inline="true" :label-position="'left'">
        <el-form-item label="附属楼房名" prop="buildingName">
          <el-autocomplete v-model="searchForm.buildingName" :fetch-suggestions="querySearchAsync" placeholder="请输入附属楼房名"
            clearable>
          </el-autocomplete>

          <!-- <el-input clearable class="input" placeholder="请输入附属楼房名" v-model="searchForm.buildingName"></el-input> -->
        </el-form-item>
        <el-form-item label="门牌号" prop="houseNumber">
          <el-input clearable class="input" placeholder="请输入门牌号" v-model="searchForm.houseNumber"></el-input>
        </el-form-item>
        <el-form-item label="是否租房" prop="rentState">
          <el-select clearable class="rentStatueSelect" v-model="searchForm.rentState" placeholder="请选择">
            <el-option label="是" value="true"></el-option>
            <el-option label="否" value="false"></el-option>
          </el-select>
        </el-form-item>
        <span class="search-button1">
          <el-form-item>
            <el-button type="primary" @click="queryRoom">搜索</el-button>
          </el-form-item>
          <el-form-item>
            <el-button @click="handleReset">重置</el-button>
          </el-form-item>
        </span>
      </el-form>
    </div>
    <!-- 清除浮动 -->
    <div style="height: 15px; "></div>
    <!-- 新增 -->
    <!-- 新增按钮、 分页条 -->
    <div>
      <el-button @click="goInsert" class="addButton" type="success" icon="el-icon-plus" size="medium">新增</el-button>
      <el-pagination class="renterPagination" @size-change="handleSizeChange" @current-change="handleCurrentChange"
        background layout="total, sizes, prev, pager, next, jumper" :total="pageTotalSize">
      </el-pagination>
    </div>

    <!-- 分页条 -->
    <!-- 表格 -->
    <el-table :data="tableData" v-loading="showLoading" style="width: 100%;font-size: 16px;"
      :header-cell-style="{ background: '#f0f0f0', color: '#333' }">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item class="subfield-title">
              <span>楼房信息</span>
            </el-form-item>
            <el-form-item label="楼房 ID">
              <span>{{ props.row.houseInfo.buildingId }}</span>
            </el-form-item>
            <el-form-item label="楼房名称">
              <span>{{ props.row.houseInfo.buildingName }}</span>
            </el-form-item>
            <el-form-item label="楼房地址">
              <span>{{ props.row.houseInfo.address }}</span>
            </el-form-item>
            <el-form-item class="subfield-title">
              <span>房屋信息</span>
            </el-form-item>
            <el-form-item label="房屋 ID">
              <span>{{ props.row.houseInfo.roomId }}</span>
            </el-form-item>
            <el-form-item label="门牌号">
              <span>{{ props.row.houseInfo.houseNumber }}</span>
            </el-form-item>
            <el-form-item class="subfield-title">
              <span>房屋状态</span>
            </el-form-item>
            <el-form-item label="租用状态">
              <span>{{ props.row.houseState.rentState ? '已租' : '未租' }}</span>
            </el-form-item>
            <div v-show="props.row.houseState.rentState">
              <el-form-item label="租用人-用户名">
                <span>{{ props.row.renterInfo.username }}</span>
              </el-form-item>
              <el-form-item label="租用人-姓名">
                <span>{{ props.row.renterInfo.name }}</span>
              </el-form-item>
              <el-form-item label="租用人-手机号">
                <span>{{ props.row.renterInfo.phoneNumber }}</span>
              </el-form-item>
              <el-form-item label="租用时间">
                <span>{{ props.row.houseState.createdTime }}</span>
              </el-form-item>
              <el-form-item label="停租时间">
                <span>{{ props.row.houseState.stopTime == null ? "未指定" : props.row.houseState.stopTime }}</span>
              </el-form-item>
            </div>

          </el-form>
        </template>
      </el-table-column>
      <el-table-column label="附属楼房名" prop="houseInfo.buildingName" align="center">
      </el-table-column>
      <el-table-column label="房屋门牌号" prop="houseInfo.houseNumber" align="center" sortable>
      </el-table-column>
      <el-table-column label="租用状态" prop="houseState.rentState" align="center">
        <template slot-scope="props">
          <span>
            {{ props.row.houseState.rentState ? '是' : '否' }}
          </span>
        </template>
      </el-table-column>
      <el-table-column prop="address" label="操作" align="center">
        <template slot-scope="props">
          <div>
            <el-button type="primary" round style="width: 45%;" @click="toUpdate(props.row)">修改
            </el-button>
            <el-button type="danger" round style="width: 45%;" @click="deleteHouse(props.row.houseInfo.roomId)">删除
            </el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>


<script>
import { queryRoom, deleteRoom } from '@/api/room'
import { getBuildingTip } from '@/api/building'

export default {
  data() {
    return {
      searchForm: {
        buildingName: '',
        houseNumber: '',
        rentState: ''

      },
      // 楼盘提示列表信息
      buildingTipList: [],
      page: 1,
      pageSize: 10,
      pageTotalSize: 20,
      tableData: [{
        //租用人信息
        renterInfo: {
          userId: '',
          name: '',
          username: '',
          phoneNumber: ''
        },
        //房屋信息
        houseInfo: {
          buildingId: '',
          buildingName: '',
          address: '',
          roomId: '',
          houseNumber: '',
        },
        //租用信息
        houseState: {
          rentingId:'',
          rentState: '',
          createdTime: '',
          stopTime: ''
        }
      }],
      showLoading: true,
      choose: "",
      search: ""
    }
  },
  computed: {
    filterData() {

      if (this.choose && this.search === "") return this.tableData.filter((item) => {
        return item.buildingInfo.buildingId === this.choose;
      })
      if (this.choose && this.search) return this.tableData.filter((item) => {
        return item.buildingInfo.buildingId === this.choose && item.houseInfo.houseNumber.indexOf(this.search) !== -1
      })
      if (!this.choose && this.search) return this.tableData.filter((item) => {
        return item.houseInfo.houseNumber.indexOf(this.search) !== -1
      })
      return this.tableData

    },
    searchBuilding() {
      let option = []
      this.tableData.forEach(item => {
        let buildingId = item.buildingInfo.buildingId
        let fall = true;
        option.forEach(item2 => {
          if (buildingId === item2.buildingId) {
            fall = false;
          }
        })
        if (fall) {
          let temp = {
            buildingId: buildingId,
            name: item.buildingInfo.name
          }
          option.push(temp)
        }
      })

      return option;
    }
  },
  methods: {
    //重置搜索栏
    handleReset() {
      this.searchForm = {
        buildingName: '',
        houseNumber: '',
        rentState: ''
      }
    },
    //搜索数据
    queryRoom() {
      this.showLoading = true;

      queryRoom(this.page,
        this.pageSize,
        this.searchForm.buildingName,
        this.searchForm.houseNumber,
        this.searchForm.rentState).then(res => {
          this.handleRoomInfo(res.data)
        })

      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        this.showLoading = false;
      }, 600 * Math.random());
    },
    //处理搜索结果集
    handleRoomInfo(data) {
      this.tableData = []
      //分页总数
      this.pageTotalSize = data.count
      data.list.forEach(item => {
        // console.log(item)
        let tableDataItem = {
          renterInfo: '',
          houseInfo: '',
          houseState: '',
        }
        //处理房屋信息
        tableDataItem.houseInfo = {
          buildingId: item.buildingId,
          buildingName: item.buildingName,
          address: item.address,
          roomId: item.roomId,
          houseNumber: item.houseNumber,
        }
        //处理租房状态
        if (item.rentState) {
          //房屋租用状态信息
          tableDataItem.houseState = {
            rentingId:item.rentingId,
            rentState: item.rentState,
            createdTime: item.createdTime.slice(0, 19).replace('T', " "),
            stopTime: item.stopTime == null ? null : item.stopTime.slice(0, 19).replace('T', " ")
          }
          //租用人信息
          tableDataItem.renterInfo = {
            userId: item.userId,
            name: item.name,
            username: item.username,
            phoneNumber: item.phoneNumber
          }
        }
        this.tableData.push(tableDataItem)
      })
    },
    //删除
    deleteHouse(roomId) {
      let i;
      for (i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].houseInfo.roomId === roomId) {
          // this.tableData.splice(i,1);
          break;
        }
      }

      this.$confirm(`此操作将删除【${this.tableData[i].houseInfo.buildingName}】门牌号为【${this.tableData[i].houseInfo.houseNumber}】的房屋信息，包含租用信息, 且无法撤销！`,
        '危险操作', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'error'
      }).then(() => {
        deleteRoom(roomId).then(()=>{
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.tableData.splice(i, 1)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });


    },
    //去更新
    toUpdate(row) {
      this.$router.push({
        path: '/manage/updateHouseInfo',
        query: {
          tableData: JSON.stringify(row)
        }
      })
    },
    //去新增
    goInsert() {
      this.$router.push({ path: "/manage/insertHouse" });
    },
    //分页条的数量
    handleSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.queryRoom();
    },
    //修改当前页
    handleCurrentChange(page) {
      this.page = page;
      this.queryRoom();
    },
    //搜索楼盘提示
    querySearchAsync(queryString, callback) {

      var options = this.buildingTipList;

      //下面的别动
      var results = queryString ? options.filter(item => {
        return (item.value.indexOf(queryString.toLowerCase()) != -1)
      }) : options;
      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        callback(results);
      }, 500 * Math.random());
    },
    //获取楼盘提示信息
    getBuildingTip() {
      getBuildingTip().then(res => {
        res.data.forEach(item => {
          let temp = {
            buildingId: item.buildingId,
            value: item.name
          }
          this.buildingTipList.push(temp)
        })
      })
    },
    // 加载数据
    loadData() {
      this.queryRoom()
      this.getBuildingTip()
    }


  },
  //通过路由规则，进入该组件时被调用
  beforeRouteEnter(to, from, next) {
    if('/manage/updateHouseInfo'== from.path){
      next((vm) => {
        vm.queryRoom()
      })      
    }
    next()
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style scoped>
/* 搜索按钮 */
.search-button1 {
  display: block;
  float: right;
}

/* 新增按钮 */
.addButton {
  margin-bottom: 20px;
}

/* 分页样式 */
.renterPagination {
  float: right;
  text-align: right;
}

/* 分栏标题 */
.subfield-title {
  color: black;
  font-weight: 600;
  margin-left: 10%
}


</style>