<template>
    <div>
        <div style="text-align:center;margin-bottom: 10px;">
            <h3>水电管理</h3>
        </div>
        <!-- 搜索栏 -->
        <div>
            <el-form class="search-form" :model="searchForm" :inline="true" :label-position="'left'">
                <el-form-item label="附属楼盘名" prop="buildingName">
                    <el-autocomplete v-model="searchForm.buildingName" :fetch-suggestions="querySearchAsync"
                        placeholder="请输入附属楼房名" clearable>
                    </el-autocomplete>
                </el-form-item>
                <el-form-item label="门牌号" prop="houseNumber">
                    <el-input clearable placeholder="请输入门牌号" v-model="searchForm.houseNumber"></el-input>
                </el-form-item>
                <el-form-item label="读数年月">
                    <el-date-picker value-format="yyyy-MM-dd HH:mm:ss" v-model="searchForm.readingDate" type="month"
                        placeholder="选择年月">
                    </el-date-picker>
                </el-form-item>

                <!-- <el-form-item label="是否出租中" prop="rentState">
                    <el-select clearable class="rentStatueSelect input" v-model="searchForm.rentState" placeholder="请选择">
                        <el-option label="是" value="true"></el-option>
                        <el-option label="否" value="false"></el-option>
                    </el-select>
                </el-form-item> -->

                <span class="search-button1">
                    <el-form-item>
                        <el-button type="primary" @click="queryMeterReading">搜索</el-button>
                    </el-form-item>
                    <el-form-item>
                        <el-button @click="handleReset">重置</el-button>
                    </el-form-item>
                </span>
            </el-form>
        </div>
        <!-- 清除浮动 -->
        <div style="height: 15px; "></div>
        <div class="add-pagination clearfix">
            <div>
                <el-button class="add" type="success" @click="toAddMeterReading">添加水电读数</el-button>
                <el-button class="add" slot="trigger" @click="batchAddDialogVisible = true"
                    type="primary">批量新增操作</el-button>
            </div>
            <el-pagination class="renterPagination" @size-change="handleSizeChange" @current-change="handleCurrentChange"
                background layout="total, sizes, prev, pager, next, jumper" :total="pageTotalSize">
            </el-pagination>
        </div>
        <el-table :data="tableData" style="width: 100%;font-size: 16px;"
            :header-cell-style="{ background: '#f0f0f0', color: '#333' }">
            <el-table-column prop="buildingName" label="楼盘名" align="center"></el-table-column>
            <el-table-column prop="houseNumber" label="房间号" align="center"></el-table-column>
            <el-table-column prop="waterReading" label="水表读数/ 吨" align="center"></el-table-column>
            <el-table-column prop="electricityReading" label="电表读数/ 度" align="center"></el-table-column>
            <el-table-column prop="readingDateVo" label="读数日期" align="center"></el-table-column>
            <el-table-column prop="address" label="操作" align="center">
                <template slot-scope="props">
                    <div>
                        <el-button type="primary" round style="width: 38%;margin-right: 5px;"
                            @click="editMeterReading(props.row)">修改
                        </el-button>
                        <el-popconfirm title="确认删除？" @confirm="deleteMeterReading(props.row)">
                            <el-button slot="reference" type="danger" round style="width: 38%;">删除
                            </el-button>
                        </el-popconfirm>

                    </div>
                </template>
            </el-table-column>

            <!-- <el-table-column label="操作" align="center">
                <template slot-scope="scope">
                    <el-button type="text" @click="editMeterReading(scope.row)">编辑</el-button>
                    <el-button type="text" @click="deleteMeterReading(scope.row)">删除</el-button>
                </template>
            </el-table-column> -->
        </el-table>
        <!-- 弹窗 -->
        <div>
            <!-- 单个新增操作 -->
            <el-dialog :visible.sync="dialogVisible" title="添加水电读数">
                <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item label="楼房号">
                        <el-select v-model="form.buildingId" @change="listRoomWhenChooseBuilding" placeholder="请选择楼房号">
                            <el-option v-for="building in buildings" :key="building.buildingId"
                                :label="building.buildingName" :value="building.buildingId"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="房间号">
                        <el-select :disabled="roomSelectAble" v-model="form.roomId" placeholder="请选择房间号" clearable>
                            <el-option v-for="room in rooms" :key="room.roomId" :label="room.houseNumber"
                                :value="room.roomId"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="读数日期">
                        <el-date-picker value-format="yyyy-MM-dd HH:mm:ss" v-model="form.readingDate" type="date"
                            placeholder="不选默认今天"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="水表读数">
                        <el-input v-model="form.waterReading"></el-input>
                    </el-form-item>
                    <el-form-item label="电表读数">
                        <el-input v-model="form.electricityReading"></el-input>
                    </el-form-item>

                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false">取 消</el-button>
                    <el-button @click="addMeterReading" type="primary">确 定</el-button>
                </div>
            </el-dialog>
            <!-- 单个修改 -->
            <el-dialog :visible.sync="dialogVisibleUpdate" title="修改水电读数记录">
                <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item label="楼房号">
                        <span> {{ form.buildingName }} </span>

                    </el-form-item>
                    <el-form-item label="房间号">
                        <span>{{ form.houseNumber }}</span>

                    </el-form-item>
                    <el-form-item label="读数日期">
                        <el-date-picker value-format="yyyy-MM-dd HH:mm:ss" v-model="form.readingDate" type="date"
                            placeholder="不选默认今天"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="水表读数">
                        <el-input v-model="form.waterReading"></el-input>
                    </el-form-item>
                    <el-form-item label="电表读数">
                        <el-input v-model="form.electricityReading"></el-input>
                    </el-form-item>

                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisibleUpdate = false">取 消</el-button>
                    <el-button @click="updateMeterReading" type="primary">确 定</el-button>
                </div>
            </el-dialog>
            <!-- 批量新增操作 -->
            <el-dialog :visible.sync="batchAddDialogVisible" title="批量新增操作">
                <h4 class="batchTitle">选择需要处理的楼盘以及时间</h4>
                <el-form ref="batchForm" :model="form" label-width="100px" style="margin-bottom: 20px;">
                    <el-form-item label="附属楼盘名" prop="buildingName">
                        <el-autocomplete v-model="batchForm.buildingName" :fetch-suggestions="querySearchAsync"
                            placeholder="请输入附属楼房名" @select="handleBatchSelect" clearable>
                        </el-autocomplete>
                    </el-form-item>
                    <el-form-item label="录入日期">
                        <el-date-picker value-format="yyyy-MM-dd HH:mm:ss" v-model="batchForm.readingDate" type="date"
                            placeholder="选择日期"></el-date-picker>
                    </el-form-item>
                </el-form>
                <div style=" margin-top: 30px;margin-left: 25px;" class="clearfix">
                    <a :href="fileUrl" download></a>
                    <el-button class="add" type="success" @click="getExcelTemplate">获取 Excel 模板</el-button>
                    <el-upload class="add upload-demo" action="" style="margin-left: 30px;" :on-change="handleUpload"
                        :before-upload="beforeUpload"
                        :accept="'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'">
                        <el-tooltip slot="trigger" class="item" effect="dark" content="自动解析楼房名与时间" placement="top">
                            <el-button type="primary">选取Excel文件</el-button>
                        </el-tooltip>
                        <div slot="tip" class="el-upload__tip">仅支持 .xlsx 格式的Excel文件</div>
                    </el-upload>
                    <el-button type="warning" @click="handleList" v-show="showHandleExcel">解析并导入</el-button>
                </div>
                <!-- 清除浮动 -->
                <div slot="footer" class="dialog-footer">
                    <el-button @click="batchAddDialogVisible = false">取 消</el-button>
                    <!-- <el-button type="primary">确 定</el-button> -->
                </div>
            </el-dialog>
        </div>

    </div>
</template>
<script>
import * as XLSX from 'xlsx'
import { getBuildingTip } from '@/api/building'
import { deleteMeterReading, queryMeterReading, addMeterReading, updateMeterReading, handleList } from '@/api/meterReading'
import { listRoomByBuildingId } from '@/api/room'
export default {
    data() {
        return {
            searchForm: {
                readingDate: '',
                buildingName: '',
                houseNumber: '',
                rentState: ''

            },
            page: 1,
            pageSize: 10,
            pageTotalSize: 20,
            // 楼盘提示列表信息
            buildingTipList: [],
            tableData: {
                Id: '',
                buildingId: '',
                buildingName: '',
                roomId: '',
                houseNumber: '',
                waterReading: '',
                electricityReading: ''
            },
            dialogVisible: false,
            //新增表单
            form: {
                buildingId: '',
                roomId: '',
                houseNumber: '',
                readingDate: '',
                waterReading: null,
                electricityReading: null
            },
            //楼盘列表
            buildings: [],
            //房屋列表
            roomSelectAble: true,
            rooms: [],
            importDialogVisible: false,
            file: null,
            // 批量新增
            batchForm: {
                buildingId: '',
                buildingName: '',
                readingDate: '',
            },
            //解析文件
            showHandleExcel: false,
            readingDate: '',
            batchAddDialogVisible: false,
            dialogVisibleUpdate: false,
            waterElectricityList: [], // 定义空数组
            excelHeader: [],
            //修改
            updateMeterReadingId: '',
            fileUrl: ''

        }
    },
    created() {
        this.queryMeterReading()
        this.getBuildingTip()
    },
    methods: {
        // 批量新增
        handleUpload(file) {
            const reader = new FileReader()
            reader.onload = (e) => {
                const data = new Uint8Array(e.target.result)
                const workbook = XLSX.read(data, { type: 'array' })
                const worksheet = workbook.Sheets[workbook.SheetNames[0]]
                // waterElectricityData 是一个包含所有 Excel 行数据的数组，可以将其传递给后端进行处理和保存
                const waterElectricityData = XLSX.utils.sheet_to_json(worksheet)
                // 获取表头
                const headerData = XLSX.utils.sheet_to_json(worksheet, { header: 1 });
                const header = headerData[0];
                this.excelHeader = header
                // 将解析出来的数据赋值给数组
                this.waterElectricityList = waterElectricityData;

                let newObj = []
                waterElectricityData.forEach((item) => {
                    let temp = {
                        buildingId: item['楼盘编号'],
                        buildingName: item['楼盘名'],
                        roomId: item['房屋编号'],
                        houseNumber: item['门牌号'],
                        waterReading: item['水记录值'],
                        electricityReading: item['电记录值'],
                    }
                    if (temp.waterReading != "" || temp.electricityReading != "") {
                        newObj.push(temp)
                    }

                })
                this.waterElectricityList = newObj

            }
            reader.readAsArrayBuffer(file.raw)
        },
        beforeUpload(file) {
            const fileType = file.name.split('.').pop()
            if (fileType !== 'xlsx') {
                this.$message.error('上传文件格式错误，请上传 .xlsx 格式的Excel文件')
                return false
            }
            // 获取文件名
            const str = file.name
            const regex = /^([^(]+)[（(](\d{4}-\d{1,2}-\d{1,2})[)）]/;
            const result = str.match(regex);

            if (result) {
                const name = result[1]; // 获取()前的名字
                const time = result[2]; // 获取()中的时间
                this.batchForm.readingDate = time + " 00:00:00"
                this.batchForm.buildingName = name
                this.buildings.forEach(item => {
                    if (item.buildingName == name) {
                        this.batchForm.buildingId = item.buildingId
                    }
                })
                this.showHandleExcel = true
            } else {
                this.$message.error('文件名格式须为：楼房名(年-月-日)')
                this.showHandleExcel = false
                return false
            }
        },
        //提交给服务器处理数据
        handleList() {
            if (this.waterElectricityList != null && this.waterElectricityList.length != 0) {
                // console.log(this.waterElectricityList)
                handleList(this.waterElectricityList, this.batchForm.readingDate).then(() => {
                    this.$message.success("数据批量导入成功！")
                    //清空操作
                    this.showHandleExcel = false
                    this.batchForm.readingDate = ''
                    this.batchForm.buildingName = ''
                    this.batchForm.buildingId = ''
                })
            }

        },
        //处理批量新增选择的楼盘
        handleBatchSelect(item) {
            this.batchForm.buildingId = item.buildingId
        },
        // 搜索水电信息
        queryMeterReading() {

            this.tableData = []
            let that = this.searchForm
            queryMeterReading(this.page, this.pageSize,
                that.buildingName, that.houseNumber,
                that.readingDate,
                // that.rentState
            ).then(res => {
                let data = res.data
                this.pageTotalSize = data.count
                data.list.forEach(item => {
                    let temp = {
                        id: item.id,
                        buildingName: item.buildingName,
                        roomId: item.roomId,
                        houseNumber: item.houseNumber,
                        readingDate: item.readingDate,
                        readingDateVo: item.readingDate.substr(0, 10),
                        waterReading: item.waterReading,
                        electricityReading: item.electricityReading
                    }
                    this.tableData.push(temp)

                })

            })
        },
        //重置搜索栏
        handleReset() {
            this.searchForm = {
                buildingName: '',
                houseNumber: '',
                readingDate: '',
                // rentState: ''
            }
        },
        //分页条的数量
        handleSizeChange(pageSize) {
            this.pageSize = pageSize;
            this.queryMeterReading()
        },
        //修改当前页
        handleCurrentChange(page) {
            this.page = page;
            this.queryMeterReading()
        },
        //单个新增
        addMeterReading() {
            addMeterReading(this.form.buildingId, this.form.roomId, this.form.readingDate, this.form.waterReading, this.form.electricityReading).then(() => {
                this.$message.success("添加成功")
                this.dialogVisible = false
                this.queryMeterReading()
            })
        },
        fetchRooms() {
            // 从后端API获取房间列表
            // ...
            this.rooms = [{ id: 1, roomId: 'A101' }, { id: 2, roomId: 'A102' }, { id: 3, roomId: 'A103' }]
        },
        toAddMeterReading() {
            this.form = {
                buildingId: '',
                roomId: '',
                houseNumber: '',
                readingDate: '',
                waterReading: null,
                electricityReading: null
            }
            this.roomSelectAble = true
            this.dialogVisible = true

        },
        //选中楼房后查询房屋
        listRoomWhenChooseBuilding() {
            this.form.houseNumber = ''
            this.roomSelectAble = false
            listRoomByBuildingId(this.form.buildingId).then(res => {
                this.rooms = []
                res.data.forEach(item => {
                    let temp = {
                        roomId: item.roomId,
                        houseNumber: item.houseNumber
                    }
                    this.rooms.push(temp)
                })
            })
        },
        editMeterReading(row) {
            this.updateMeterReadingId = row.id
            this.form = {
                buildingId: '',
                buildingName: row.buildingName,
                roomId: row.roomId,
                houseNumber: row.houseNumber,
                readingDate: row.readingDate,
                waterReading: row.waterReading,
                electricityReading: row.electricityReading
            }
            this.dialogVisibleUpdate = true
        },
        updateMeterReading() {
            updateMeterReading(
                this.updateMeterReadingId,
                this.form.readingDate,
                this.form.waterReading,
                this.form.electricityReading
            ).then(() => {
                this.$message.success("修改成功")
                this.dialogVisibleUpdate = false
                this.queryMeterReading()
            })
        },
        deleteMeterReading(row) {
            deleteMeterReading(row.id).then(() => {
                this.$message.success("删除成功")
                this.queryMeterReading()
            })
        },
        //获取Excel模板
        getExcelTemplate() {
            if (this.batchForm.buildingId == "" || this.batchForm.readingDate == "") {
                this.$message.error("楼房名与时间不能为空")
                return false
            }
            listRoomByBuildingId(this.batchForm.buildingId).then(res => {
                if (res.data == null || res.data.length == 0) {
                    this.$message.error("该楼房不存在任何房屋")
                    return false
                } else {
                    this.$message.info("服务器处理中,请稍后~")
                    let a = document.createElement("a");
                    a.href = `http://localhost:81/manage/meterReading/downloadExcel?buildingId=${this.batchForm.buildingId}&readingDate=${this.batchForm.readingDate}`;
                    a.click()
                    this.$notify({
                        title: '警告',
                        message: '模板不能新加行，也不能修改前4列数据，只能新增与修改水电！',
                        type: 'warning',
                        position: 'top-left'
                    });
                }
            })


        },
        //搜索楼盘提示
        querySearchAsync(queryString, callback) {

            var options = this.buildingTipList;

            //下面的别动
            var results = queryString ? options.filter(item => {
                return (item.value.indexOf(queryString.toLowerCase()) != -1)
            }) : options;
            clearTimeout(this.timeout);
            this.timeout = setTimeout(() => {
                callback(results);
            }, 500 * Math.random());
        },
        //获取楼盘提示信息
        getBuildingTip() {
            this.buildingTipList = []
            this.buildings = []
            getBuildingTip().then(res => {
                res.data.forEach(item => {
                    let temp = {
                        buildingId: item.buildingId,
                        value: item.name
                    }
                    this.buildingTipList.push(temp)
                    let temp2 = {
                        buildingId: item.buildingId,
                        buildingName: item.name
                    }
                    this.buildings.push(temp2)
                })
            })
        },
    }
}
</script>
<style scoped>
/* 搜索按钮 */
.search-button1 {
    display: block;
    float: right;
}

/* 表格样式 */
.input {
    float: left;
    width: 120px;
}

/* 新增按钮 */
.add-pagination {
    margin-bottom: 10px;
}

/* 分页样式 */
.renterPagination {
    float: right;
    text-align: right;
}

/* 新增区域 */
.add {
    float: left;
}

.batchTitle {
    padding-left: 30px;
    padding-bottom: 20px;
}
</style>



