<template>
  <div>
    <div style="text-align:center;">
      <h3>修改房屋信息</h3>
    </div>
    <div v-if="fall">
      <el-form ref="form" :model="tableData" label-width="150px">
        <el-form-item label="附属楼房名">
          <span style="font-size: 16px;margin-left: 35px">{{ tableData.houseInfo.buildingName }}</span>
        </el-form-item>
        <el-form-item label="门牌号" disabled>
          <span style="font-size: 16px;margin-left: 35px">{{ tableData.houseInfo.houseNumber }}</span>

        </el-form-item>
        <el-form-item label="租用状态">
          <el-tooltip class="item" effect="dark" content="通过修改租用或停租时间，从而改变租用状态" placement="right">
            <el-switch disabled v-model="tableData.houseState.rentState" active-text="已租" inactive-text="空置">
            </el-switch>
          </el-tooltip>
        </el-form-item>
        <el-form-item label="租用人">
          <el-autocomplete v-model="state" :fetch-suggestions="querySearchAsync" placeholder="用户名（姓名）" clearable
            @select="handleSelect">
          </el-autocomplete>
        </el-form-item>
        <el-form-item label="租用时间">
          <el-date-picker value-format="yyyy-MM-dd HH:mm:ss" v-model="tableData.houseState.createdTime" type="datetime"
            placeholder="若不选，则默认当前时间">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="长期租用">
          <el-switch v-model="longRengting" active-text="是" inactive-text="否">
          </el-switch>
        </el-form-item>
        <el-form-item label="停租时间" v-show="!longRengting">
          <el-date-picker @change="setStopTimeTip" value-format="yyyy-MM-dd HH:mm:ss"
            v-model="tableData.houseState.stopTime" type="datetime" placeholder="选择停租时间">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit(true)">确认修改</el-button>
          <el-button @click="$router.go(-1)">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div v-else style="text-align: center;color: red;font-size: 30px;padding: 50px 0">
      <h1>错误！！请先在左边查询房屋信息！</h1>
    </div>

  </div>
</template>

<script>
import { updateRenting } from '@/api/renting'
export default {
  name: "UpdateHouseInfo",
  data() {
    return {
      // 只是用来显示罢了
      state: '',
      fall: false,
      tableData: {
        //租用人信息
        renterInfo: {
          userId: '',
          name: '',
          username: '',
          phoneNumber: ''
        },
        //房屋信息
        houseInfo: {
          buildingId: '',
          buildingName: '',
          address: '',
          roomId: '',
          houseNumber: '',
        },
        //租用信息
        houseState: {
          rentingId: '',
          rentState: '',
          createdTime: '',
          stopTime: ''
        }
      },
      currentUserId: null,
      options: [],
      renter: [],
      longRengting: false
    }
  },
  methods: {
    querySearchAsync(queryString, callback) {
      var renter = this.renter;
      var results = queryString ? renter.filter(this.createStateFilter(queryString)) : renter;
      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        callback(results);
      }, 500 * Math.random());
    },
    createStateFilter(queryString) {
      return (state) => {
        return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) != -1);
      };
    },
    handleSelect(item) {
      this.currentUserId = item.userId
    },
    onSubmit() {
      // rentingId, userId, roomId, createdTime, stopTime
      let data = this.tableData
      updateRenting(data.houseState.rentingId,
        this.currentUserId,
        data.houseInfo.roomId,
        data.houseState.createdTime,
        this.longRengting ? null : data.houseState.stopTime).then(() => {
          this.$message.success("修改成功")
          this.$router.go(-1);
        })
    },
    //设置停租时间提示
    setStopTimeTip() {

      this.$notify({
        title: '警告',
        message: '若设定的停租时间到期了，那么该房屋与该租客的租用关系将结束，房屋立刻转为未租用状态，原租用记录将无法显示，但完全不影响订单缴费',
        type: 'warning'
      });
    },
    //路由进来，对数据进行初始化
    initData(){
      //初始化数据
      if (this.$route.query.tableData !== undefined) {

        let tableOrginData = JSON.parse(this.$route.query.tableData)
        if (tableOrginData.houseState.rentState) {
          this.tableData = JSON.parse(this.$route.query.tableData)
        } else {
          this.tableData.houseInfo = tableOrginData.houseInfo
        }
        //显示租用人
        if (tableOrginData.renterInfo.username != null && tableOrginData.renterInfo.name != null) {
          this.state = `${tableOrginData.renterInfo.username}（${tableOrginData.renterInfo.name}）`
          this.currentUserId = tableOrginData.renterInfo.userId
        }
        //处理停租时间
        if (this.tableData.houseState.stopTime == null) this.longRengting = true
        this.fall = true;
      }
    },
    //获取租客列表
    queryRenter(){
      this.$axios.get(this.$store.state.publicLocation + "/manage/listRenter").then(response => {
        let data = response.data
        data.forEach(element => {
          let renterTemp = {
            value: element.username + "（" + element.name + "）",
            userId: element.userId
          }
          this.renter.push(renterTemp)
        });

      })
    }
  },
  created() {
    this.initData()
    this.queryRenter()
    
  }
}
</script>

<style scoped>
.el-form {
  margin-top: 20px;
  padding: 0 50px;
}
</style>