<template>
  <div>
    <el-form class="search-form" :model="searchForm" :inline="true" :label-position="'left'">
      <el-form-item label="用户名" prop="username">
        <el-input class="input" placeholder="请输入用户名" v-model="searchForm.username"></el-input>
      </el-form-item>
      <el-form-item label=" 姓名" prop="name">
        <el-input class="input" placeholder="请输入姓名" v-model="searchForm.name"></el-input>
      </el-form-item>
      <el-form-item label="电话号码" prop="phone">
        <el-input class="input" placeholder="请输入电话号码" v-model="searchForm.phone"></el-input>
      </el-form-item>
      <el-form-item label="是否租房" prop="rentState">
        <el-select class="rentStatueSelect" v-model="searchForm.rentState" placeholder="请选择">
          <el-option label="是" :value="true"></el-option>
          <el-option label="否" :value="false"></el-option>
        </el-select>
      </el-form-item>
      <span class="search-button">
        <el-form-item>
          <el-button type="primary" @click="queryRenter">搜索</el-button>
        </el-form-item>
        <el-form-item>
          <el-button @click="handleReset">重置</el-button>
        </el-form-item>
      </span>
    </el-form>
    <!-- 清除浮动 -->
    <div style="height: 15px; "></div>

    <!-- 新增按钮、 分页条 -->
    <div>
      <el-button @click="goInsert" class="addButton" type="success" icon="el-icon-plus" size="medium">新增</el-button>
      <el-pagination class="renterPagination" @size-change="handleSizeChange" @current-change="handleCurrentChange"
        background layout="total, sizes, prev, pager, next, jumper" :total="pageTotalSize">
      </el-pagination>
    </div>
    <!-- 数据表格 -->
    <el-table :data="filterData" v-loading="showLoading" element-loading-text="数据正在加载中..."
      :header-cell-style="{ background: '#f0f0f0', color: '#333' }">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item style="color: black;margin-left: 10%">
              <h3>详细个人信息</h3>
            </el-form-item>
            <el-form-item label="用户 ID">
              <span>{{ props.row.userId }}</span>
            </el-form-item>
            <el-form-item label="角色 ID">
              <span>{{ props.row.roleId }}</span>
            </el-form-item>
            <el-form-item label="角色描述">
              <span>{{ props.row.roleDescription }}</span>
            </el-form-item>
            <el-form-item label="用户名">
              <span>{{ props.row.username }}</span>
            </el-form-item>
            <el-form-item label="性别">
              <span>{{ props.row.sex === "2" ? "未知" : props.row.sex === "1" ? "男" : "女" }}</span>
            </el-form-item>
            <el-form-item label="姓名">
              <span>{{ props.row.name }}</span>
            </el-form-item>
            <el-form-item label="电话号码">
              <span>{{ props.row.phoneNumber }}</span>
            </el-form-item>
            <el-form-item label="身份证号码">
              <span>{{ props.row.identity }}</span>
            </el-form-item>
            <el-form-item label="锁定状态">
              <span>{{ props.row.locked ? '已锁定' : '未锁定' }}</span>
            </el-form-item>
            <el-form-item label="可用状态">
              <span>{{ props.row.enable ? '账户可用' : '账户不可用' }}</span>
            </el-form-item>
            <el-form-item label="是否租房">
              <span>{{ props.row.rentState ? '是' : '否' }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column label="用户名" prop="username" align="center">
      </el-table-column>
      <el-table-column label="姓名" prop="name" align="center">
      </el-table-column>
      <el-table-column label="电话号码" prop="phoneNumber" align="center">
      </el-table-column>
      <el-table-column label="是否租房" prop="houseState.rentState" align="center">
        <template slot-scope="props">
          <span>
            {{ props.row.rentState ? '是' : '否' }}
          </span>
        </template>
      </el-table-column>
      <el-table-column prop="address" label="操作" align="center">
        <template slot-scope="props">
          <div>
            <el-button type="primary" round style="width: 45%;" @click="$router.push({
              path: '/manage/updateRenter',
              query: {
                isHasData: true,
                tableData: JSON.stringify(filterData[props.$index])
              }
            })">修改
            </el-button>
            <el-button type="danger" round style="width: 45%;" @click="triggerDeleteRenter(props, props.row.userId)">删除
            </el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <!-- 删除确认框 -->
    <div>
      <el-dialog class="deletef" title="确认删除" :visible.sync="confirmDialogVisible" :close-on-click-modal="false"
        :close-on-press-escape="false" width="30%">
        <p class="contentp">{{ message }}</p>
        <div class="buttonf">

          <div v-if="countdown > 0" class="button">
            <el-button type="danger" :disabled="true">删除（{{ countdown }}S）</el-button>
            <el-button @click="confirmDialogVisible = false">取消</el-button>
          </div>

          <div v-else class="button">
            <el-button type="danger" :loading="deleting" @click="deleteRenter">删除</el-button>
            <el-button @click="confirmDialogVisible = false">取消</el-button>
          </div>

        </div>
        <div class="empty"></div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { queryRenter, deleteRenter, getTotalRenter } from '@/api/renter'


export default {
  name: "QueryRenter",
  data() {
    return {
      search: "",
      tableData: [],
      showLoading: true,
      page: 1,
      pageSize: 10,
      pageTotalSize: 20,
      searchForm: {
        username: "",
        name: "",
        phoneNumber: "",
        rentState: ""
      },
      //删除确认框
      confirmDialogVisible: false,
      countdown: 0,
      deleting: false,
      countdownIntervalId: null,
      message:'',
      readyDeleteProps:'',
      readyDeleteUserId: '',
    };
  },
  computed: {
    filterData() {
      return this.tableData.filter((item) => {
        return item.username.indexOf(this.search) !== -1;
      });
    }
  },
  methods: {
    //重置搜索条
    handleReset() {
      this.searchForm = {
        username: "",
        name: "",
        phoneNumber: "",
        rentState: ""
      };
    },
    //获取租客列表
    queryRenter() {
      this.tableData = [];
      this.showLoading = true;
      let rentState = "";
      switch (this.searchForm.rentState) {
        case "1":
          rentState = true;
          break;
        case "0":
          rentState = false;
          break;
        default: rentState = "";
      }
      queryRenter(this.page, this.pageSize, this.searchForm.username, this.searchForm.name, this.searchForm.phoneNumber, rentState).then(response => {
        response.data.forEach(data => {
          let temp = {
            userId: data.uUserVol.userId,
            roleId: data.uUserVol.roleId,
            roleDescription: data.uRole.description,
            username: data.uUserVol.username,
            password: "",
            sex: data.uUserVol.sex,
            name: data.uUserVol.name,
            phoneNumber: data.uUserVol.phoneNumber,
            identity: data.uUserVol.identity,
            locked: data.uUserVol.locked === "1",
            enable: data.uUserVol.enable === "1",
            createdTime: "2021-11-20 13:19:00",
            updateTime: data.uUserVol.updateTime,
            rentState: data.rentState
          };
          this.tableData.push(temp);
        });
      });
      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        this.showLoading = false;
      }, 600 * Math.random());
    },
    //获取租客数量
    getTotalRenter() {
      getTotalRenter().then(response => {
        this.pageTotalSize = response.data;
      });
    },
    //监视删除功能，删除最后一条时候刷新数据
    handleDelete(index) {
      let result = (this.page === Math.ceil(this.pageTotalSize / this.pageSize)) && index === 0 && this.tableData.length === 0;
      if (result) {
        if (this.page > 1)
          this.page--;
        this.loadData();
      }
    },
    //触发删除租客
    triggerDeleteRenter(props, userId) {
      //触发二次删除确认框显示
      this.showConfirmDialog()
      //准备数据
      this.readyDeleteProps=props
      this.readyDeleteUserId=userId
      //扫描租客
      let i;
      for (i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].userId === userId) {
          break;
        }
      }
      this.message = `此操作将删除用户名为【${this.tableData[i].username}】，姓名为【${this.tableData[i].name}】信息，包含租用信息, 且无法撤销！`
      
    },
    //删除租客
    deleteRenter(){
      deleteRenter(this.readyDeleteUserId).then(response => {
        if (response.data) {
          this.$message({
            type: "success",
            message: "删除成功!"
          });
          //扫描租客
          let i;
          for (i = 0; i < this.tableData.length; i++) {
            if (this.tableData[i].userId === this.readyDeleteUserId) {
              break;
            }
          }
          this.tableData.splice(i, 1);
          this.handleDelete(this.readyDeleteProps.$index);

          this.getTotalRenter();
          this.confirmDialogVisible = false
          clearInterval(this.countdownIntervalId)
        }
        else {
          this.$message({
            type: "error",
            message: "删除失败！"
          });
        }
      });

    },
    //前往插入页面
    goInsert() {
      this.$router.push({ path: "/manage/insertRenter" });
    },
    //分页条的数量
    handleSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.queryRenter();
    },
    //修改当前页
    handleCurrentChange(page) {
      this.page = page;
      this.queryRenter();
    },
    //加载数据和分页数据
    loadData() {
      this.queryRenter();
      this.getTotalRenter();
    },
    //显示确认框
    showConfirmDialog() {
      this.confirmDialogVisible = true;
      this.countdown = 2;
      this.countdownIntervalId = setInterval(() => {
        this.countdown--;
        if (this.countdown === 0) {
          clearInterval(this.countdownIntervalId);
        }
      }, 1000);
    },
    //确认删除
    deleteItem() {
      this.deleting = true;
      // TODO: 在此处完成删除操作
      setTimeout(() => {
        this.deleting = false;
        this.confirmDialogVisible = false;
      }, 1000);
    },
  },
  mounted() {
    this.loadData()
  },
  components: {}
}
</script>

<style scoped>
/* 分页样式 */
.renterPagination {
  float: right;
  text-align: right;
}

/* 表格样式 */
.input {
  float: left;
  width: 170px;
}

/* 新增按钮 */
.addButton {
  margin-bottom: 20px;
}

/* 下拉框 */
.rentStatueSelect {
  width: 110px;
}

/* 搜索按钮 */
.search-button {
  display: block;
  float: right;
}
/* 删除确认框样式 */
.contentp{
    padding: 0 15px 15px 15px;
}
.buttonf {
    height: 38px;
}

.button {
    float: right;
}
</style>