<template>
  <div>
    <!-- 头部 -->
    <header style="text-align:center;margin-bottom: 10px;">
      <h3>楼盘管理</h3>
    </header>
    <main>
      <!-- 搜索栏 -->
      <el-form :model="searchForm" :inline="true" :label-position="'left'">
        <el-form-item label="楼盘名" prop="name">
          <el-input class="input" placeholder="请输入楼盘名" v-model="searchForm.name"></el-input>
        </el-form-item>
        <el-form-item label="地址" prop="address">
          <el-input class="input" placeholder="请输入地址" v-model="searchForm.address"></el-input>
        </el-form-item>
        <div class="search-button">
          <el-form-item>
            <el-button type="primary" @click="queryBuilding">搜索</el-button>
          </el-form-item>
          <el-form-item>
            <el-button @click="handleReset">重置</el-button>
          </el-form-item>
        </div>

      </el-form>
      <!-- 清除浮动 -->
      <div style="height: 15px; "></div>
      <!-- 新增与分页条 -->
      <div>
        <el-button class="addButton" @click="goInsert" type="success" icon="el-icon-plus" size="medium">新增</el-button>
        <el-pagination class="renterPagination" @size-change="handleSizeChange" @current-change="handleCurrentChange"
          background layout="total, sizes, prev, pager, next, jumper" :total="pageTotalSize">
        </el-pagination>
      </div>
      <!-- 表格内容 -->
      <el-table :data="tableData" stripe border v-loading="showLoading" style="width: 100%;font-size: 16px;"
        :header-cell-style="{ background: '#f0f0f0', color: '#333' }">
        <el-table-column prop="name" label="楼房名" width="180" sortable align="center">
          <template slot-scope="scope">
            <span v-if="tableData[scope.$index].editing">
              <el-input placeholder="楼房名字" v-model="scope.row.name"></el-input>
            </span>
            <span v-else style="margin-left: 10px">{{ scope.row.name }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="address" label="地址" width="300" align="center">
          <template slot-scope="scope">
            <span v-if="tableData[scope.$index].editing">
              <el-input placeholder="楼房地址" v-model="scope.row.address"></el-input>
            </span>
            <span v-else style="margin-left: 10px">{{ scope.row.address }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="createdTime" label="创建时间" align="center">
          <template slot-scope="scope">


            <span>
              <span style="margin-left: 10px">{{ scope.row.createdTime }}</span>
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="address" label="操作" align="center">
          <template slot-scope="scope">
            <div>
              <el-button v-show="!tableData[scope.$index].editing" type="primary" round
                @click="tableData[scope.$index].editing = true" style="width: 45%;">修改</el-button>
              <el-button v-show="!tableData[scope.$index].editing" type="danger" round
                @click="deleteInfo(scope, scope.$index)" style="width: 45%;">删除</el-button>
              <el-button v-show="tableData[scope.$index].editing" type="success" round @click="updateBuilding(scope.$index)"
                style="width: 100%;margin: 0">确认变更</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </main>

  </div>
</template>

<script>
import { queryBuilding, deleteBuilding, updateBuilding } from '@/api/building'
export default {
  name: "BuildingManage",
  components: {},
  data() {
    return {
      searchForm: {
        name: '',
        address: ''
      },
      tableData: [],
      page: 1,
      pageSize: 10,
      pageTotalSize: 10,
      showLoading: true
    }
  },
  methods: {
    // 搜索处理
    queryRenter() {

    },
    // 重置搜索
    handleReset() {
      this.searchForm = {
        name: '',
        address: ''
      }
    },
    // 页大小变化
    handleSizeChange(pageSize) {
      this.pageSize = pageSize
      this.queryBuilding()
    },
    // 页码变化
    handleCurrentChange(page) {
      this.page = page
      this.queryBuilding()
    },
    //去新增楼盘
    goInsert() {
      this.$router.push({ path: '/manage/insertBuilding' })
    },
    //查询楼盘
    queryBuilding() {
      this.showLoading = true;
      this.tableData = []

      queryBuilding(this.page, this.pageSize, this.searchForm.name, this.searchForm.address).then(res => {
        res.data.list.forEach(item => {
          let temp = {
            buildingId: "",
            name: '',
            address: "",
            createdTime: "",
            editing: false
          }
          temp.buildingId = item.buildingId
          temp.name = item.name
          temp.address = item.address
          temp.createdTime = item.createdTime.slice(0, 19).replace('T', " ")
          this.tableData.unshift(temp)
        })
        this.pageTotalSize = res.data.count
        clearTimeout(this.timeout);
        this.timeout = setTimeout(() => {
          this.showLoading = false;
        }, 600 * Math.random());
      })
    },
    //监视删除功能，删除最后一条时候刷新数据
    handleDelete(index) {
      let result = (this.page === Math.ceil(this.pageTotalSize / this.pageSize)) && index === 0 && this.tableData.length === 0
      if (result) {
        if (this.page > 1) {
          this.page--;
        }
        this.queryBuilding()
      }
    },
    deleteInfo(scope, tableDataIndex) {
      this.$confirm(`此操作将删除楼房【${this.tableData[tableDataIndex].name}】所有信息，包括其下的所有房屋信息,水电信息，租用信息, 是否继续?`,
        '危险操作', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'error'
      }).then(() => {
        //执行删除
        deleteBuilding(this.tableData[tableDataIndex].buildingId).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.tableData.splice(tableDataIndex, 1)
          this.handleDelete(scope.$index)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    updateBuilding(tableDataIndex) {
      updateBuilding(this.tableData[tableDataIndex].buildingId,this.tableData[tableDataIndex].name, this.tableData[tableDataIndex].address).then(()=>{
        this.$message.success("修改成功！")
        this.tableData[tableDataIndex].editing = false;
      })
    },
    addBuilding(tableDataIndex) {
      let temp = this.tableData[tableDataIndex];
      this.$axios.get(this.$store.state.publicLocation + "/manage/addBuilding", {
        params: {
          name: temp.name,
          address: temp.address
        }
      }).then(response => {
        if (response.data) {
          temp.editing = false;
          this.$message.success("添加楼房成功")
        }
      })


    }
  },
  created() {
    this.queryBuilding();
  },
  mounted() {
    // alert(this.tableData.length)
  }
}
</script>

<style scoped>
/* 搜索样式 */
.input {
  float: left;
  width: 170px;
}

.search-button {
  float: right;
}

/* 新增按钮 */
.addButton {
  margin-bottom: 20px;
}

/* 分页样式 */
.renterPagination {
  float: right;
  text-align: right;
}
</style>