import request from '@/request'
let url = '/manage/renter'

//【增】新增租客
export function addRenter(
    roleId, name, sex, identity, username,
    password, phoneNumber, locked, enable) {
    return request({
        url,
        method: 'post',
        params: {
            roleId: roleId,
            name: name,
            sex: sex,
            identity: identity,
            username: username,
            password: password,
            phoneNumber: phoneNumber,
            locked: locked,
            enable: enable
        }
    })
}

//【删】通过userId删除租客
export function deleteRenter(userId) {
    return request({
        url,
        method: 'delete',
        data: {
            userId: userId,
        }
    })
}
//【改】修改租客信息
export function updateRenter(renter) {
    return request({
        url,
        method: "put",
        data: renter
    })
}
//【查】分页租客
export function queryRenter(page, pageSize, username, name, phoneNumber, rentState) {
    return request({
        url: "/manage/renterItemVolList",
        method: 'get',
        params: {
            page: page,
            pageSize: pageSize,
            username: username,
            name: name,
            phoneNumber: phoneNumber,
            rentState: rentState
        }
    })
}

//【查】租客的总数量
export function getTotalRenter() {
    return request({
        url: "/manage/totalRenter",
        method: 'get'
    })
}

//【查】所有用户角色
export function queryRoles() {
    return request({
        url: "/manage/role",
        method: "get"
    })
}



