import request from '@/request'

let url ='/manage/renting'
export function updateRenting(rentingId,userId,roomId,createdTime,stopTime){
    return request({
        url,
        method:'put',
        data: {
            rentingId:rentingId,
            userId:userId,
            roomId:roomId,
            createdTime:createdTime,
            stopTime:stopTime
        }
    })
}
export function rentingWeek(){
    return request({
        url:'/manage/rentingWeek',
        method:'get'

    })
}