import request from '@/request'

let url = '/manage/room'
// 查询详细房屋数据，关联4张表
export function queryRoom(page,pageSize,buildingName,houseNumber,rentState){
    return request({
        url,
        method:'get',
        params:{
            page:page,
            pageSize:pageSize,
            buildingName:buildingName,
            houseNumber:houseNumber,
            rentState:rentState
        }
    })
}
//增加
export function addRoom(buildingId,houseNumber){
    return request({
        url,
        method:'post',
        data:{
            buildingId:buildingId,
            houseNumber:houseNumber
        }
    })
}
//删除
export function deleteRoom(roomId){
    return request({
        url,
        method:'delete',
        params:{
            roomId:roomId
        }
    })
}
//查询房屋列表 id和房屋名
export function listRoomByBuildingId(buildingId){
    return request({
        url:'manage/listRoomByBuildingId',
        method:'get',
        params:{
            buildingId:buildingId
        }
    })
}

export function roomCountInBuilding(){
    return request({
        url:'manage/roomCountInBuilding',
        method:'get'
    })
}
