<template>
  <el-container>
    <!--        右侧顶边栏-->
    <el-header>
      <div class="left" style="float:left;">
        <h1>
          <span class="collapse-icon">
            <svg @click="toChangeCollapse" viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg" width="64" height="64"
              class="hamburger is-active">
              <path
                d="M408 442h480c4.4 0 8-3.6 8-8v-56c0-4.4-3.6-8-8-8H408c-4.4 0-8 3.6-8 8v56c0 4.4 3.6 8 8 8zm-8 204c0 4.4 3.6 8 8 8h480c4.4 0 8-3.6 8-8v-56c0-4.4-3.6-8-8-8H408c-4.4 0-8 3.6-8 8v56zm504-486H120c-4.4 0-8 3.6-8 8v56c0 4.4 3.6 8 8 8h784c4.4 0 8-3.6 8-8v-56c0-4.4-3.6-8-8-8zm0 632H120c-4.4 0-8 3.6-8 8v56c0 4.4 3.6 8 8 8h784c4.4 0 8-3.6 8-8v-56c0-4.4-3.6-8-8-8zM142.4 642.1L298.7 519a8.84 8.84 0 0 0 0-13.9L142.4 381.9c-5.8-4.6-14.4-.5-14.4 6.9v246.3a8.9 8.9 0 0 0 14.4 7z">
              </path>
            </svg>
          </span>

          <span class="title">出租房管理系统</span>
        </h1>
      </div>
      <!-- 移动端变化 -->
      <div v-show="!isMobile" class="right" style="float:right;">
        <div class="user">
          <div class="userLogo">
            <img src="../../../assets/static/tx.jpg" alt="">
          </div>
        </div>
        <el-dropdown>
          <span>
            <span class="username">{{ username }}</span>
            <i class="el-icon-caret-left" style="margin-right: 15px">
            </i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>个人中心</el-dropdown-item>
            <el-dropdown-item>
              <a @click.prevent="logout" href="#">退出登录</a>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-header>
    <!--        核心内容-->
    <el-main >
     <!-- {{ $route.meta.keepAlive != null?"true":"false"}} -->
      <transition name="fade" mode="out-in">
        <keep-alive>
          <router-view v-if="$route.meta.keepAlive"></router-view>
        </keep-alive>
      </transition>
      <router-view v-if="!$route.meta.keepAlive"></router-view>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: "MMain",
  props: {
    changeCollapse: {
      type: Function,
    }
  },
  data() {
    return {
      username: this.$cookies.get("username")
    }
  },
  computed: {
    isMobile() {
      return this.$store.state.isMobile
    },
    publicLocation() {
      return this.$store.state.publicLocation;
    }
  },
  methods: {
    logout() {
      this.$confirm(`是否退出${this.username}的登录状态?`, '温馨提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '退出成功!'
        });
        this.$cookies.remove("username");
        this.$cookies.remove("JSESSIONID");
        this.$axios.get(this.publicLocation + "/logout")
        this.$router.push({ path: "/login" })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        });
      });
    },
    //变化侧边栏
    toChangeCollapse(){
      this.changeCollapse()

    }
  },
  created(){
    
  }
}
</script>

<style scoped>
/*头部部分*/
.el-header {
  position: sticky;
  top: 0;
  background-color: white;
  font-size: 12px;
  color: #333;
  line-height: 60px;
  z-index: 1;
}

.hamburger {
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px
}

.hamburger.is-active {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg)
}

/*头部左边*/
.left h1 {
  font-size: 22px;
}

.title {
  margin-left: 15px;
}

.collapse-icon {
  cursor: pointer;
}

/*头部右边*/
/*user部分*/
.user {
  display: inline-block;
}

.user .username {
  margin: 0 10px;
  font-size: 15px;
}

/*logo部分*/
.userLogo {
  display: inline-block;
  height: 40px;
  width: 40px;
}

.userLogo>img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  vertical-align: middle;
}

/*下拉部分*/
.el-dropdown .username {
  margin: 0 10px;
  font-size: 15px;
}

.el-dropdown .el-icon-caret-left {
  font-size: 20px;
  transition: all 0.5s;
}

.el-dropdown .username:hover+.el-icon-caret-left,
.el-dropdown .el-icon-caret-left:hover {
  transform: rotate(-90deg);
}

/*头部结束*/
/*核心内容*/
.el-main {
  background-color: #f8f8f8;
  padding: 20px;
  height: 100%;
}

.el-aside {
  color: #333;
}
</style>