<template>
  <div>
    <div>
      <div style="text-align:center;"><h3>录入房屋</h3></div>
      <el-form ref="form" :model="tableData" label-width="100px">
        <el-form-item label="附属楼盘名">
          <el-select v-model="tableData.buildingId" clearable placeholder="选择楼房">
            <el-option
                v-for="item in tableData.options"
                :key="item.buildingId"
                :label="item.name"
                :value="item.buildingId">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="门牌号">
          <el-input v-model="tableData.houseNumber" style="width: 21%;"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addRoom">提交</el-button>
          <el-button @click="$router.go(-1)">返回</el-button>
        </el-form-item>
      </el-form>

    </div>
  </div>
</template>

<script>
import { getBuildingTip } from '@/api/building'
import { addRoom } from '@/api/room'
export default {
  name: "InsertHouse",
  data() {
    return {
      tableData: {
        buildingId:'',
        houseNumber:"",
        options: [],
      },
      rules:{
        username:[
          {required: true, message: '请输入用户名', trigger: 'blur'},
          {min:4,max:20,message: '用户名长度不少于4',trigger: 'blur'}
        ],
        password:[
          {required: true, message: '请输入密码', trigger: 'blur'},
          {min:6,message: '密码至少为6位',trigger: 'blur'}
        ]
      }

    }
  },
  methods:{
    //校验信息
    validateRoomInfo(){
      if (this.tableData.buildingId <= 0 ||
        this.tableData.houseNumber === "" ||
        !/^[0-9]+$/.test(this.tableData.houseNumber)||
        !/^[0-9]+$/.test(this.tableData.buildingId)) {
        this.$message.error("数据格式不正确")
        return false
      }
      return true
    },
    //添加房屋
    addRoom(){
      //验证信息合法性
      if(!this.validateRoomInfo()) return
      //合法
      addRoom(this.tableData.buildingId, this.tableData.houseNumber).then(()=>{
         this.$message.success("添加成功！")
         this.tableData.houseNumber = "";
      })

      //注意，这里还需要去数据库中查找水电编号，避免冲突！！！！，水电编号全房屋唯一

      // ajax

    },
    // 楼盘提示
    getBuildingTip(){
      getBuildingTip().then(res=>{
        res.data.forEach(item => {
          let temp = {
            buildingId: item.buildingId,
            name: item.name
          }
          this.tableData.options.push(temp)

        })
      })
    }
  },
  created() {
    this.getBuildingTip()
  }
}
</script>

